import React from 'react';
import { graphql } from 'gatsby';
import Image from 'gatsby-image';
import Layout from './layout';
import styled from '@emotion/styled';

const ListarArticulos = styled.ul`
  max-width:1200px;
  width:95%;
  margin:4rem auto 0 auto;

  `

export const query = graphql`
    query($slug: String!) {
        allDatoCmsArticulo(filter: { slug: { eq: $slug } }) {
            nodes {
                    titulo
                    id
                  descripcion
                categoria
                  tag
                  imgdestacada {
                    fluid {
                        ...GatsbyDatoCmsFluid
                    }
                }
            }
        }
    }
`

const ArticuloTemplate = ({ data: { allDatoCmsArticulo: { nodes } } }) => {

    const { imgdestacada, descripcion,  titulo, tag, categoria, } = nodes[0];

    return (
        <Layout>
            <main className="w-2/3 mx-auto my-8 list-none">
                <h1 className="text-center m-8">{titulo}2</h1>
                <p>{descripcion} </p>
                <br></br><br></br><b>tag: </b>{tag}
                <br></br><br></br><b>categoria: </b>
                <ListarArticulos>
                    <li>{categoria}</li>
                </ListarArticulos>
                <Image className="w-3/5 mx-auto"
                    fluid={imgdestacada.fluid}
                />
            </main>
        </Layout>
    );
}

export default ArticuloTemplate;